import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ViewCell } from "ng2-smart-table";

import {SmartTableDatePickerComponent  } from '../../../@core/utils/smart-table-date-picker.component';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Http } from '@angular/http';
import { RemoteDataSource, ActionConfirm } from "../../../@core/utils/remoteDataSource"
import { ConventULR } from "../../pages.component"
import { ToasterService } from 'angular2-toaster';
import { SmartTableService } from '../../../@core/data/smart-table.service';
@Component({
  selector: 'ngx-modal',
  template: `
    <div class="modal-header">
      <span>{{ modalHeader }}</span>
      <button class="close" aria-label="Close" (click)="closeModal()">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div class="modal-body">
    <ng2-smart-table [settings]="settings" 
    [source]="source"
     (createConfirm)="onCreateConfirm($event)"
     (deleteConfirm)="onDeleteConfirm($event)"
     (editConfirm)="onEditConfirm($event)">
    </ng2-smart-table>
     </div>
    <div class="modal-footer">
      <button class="btn btn-md btn-primary" (click)="closeModal()">关闭</button>
    </div>
  `,
})
export class ModalComponent {
  source: any;
  modalHeader: string;
  projectID: number;
  private do_add = 0
  type_filter = [
    { value: '普通用户', title: '普通用户' },
    { value: '运维用户', title: '运维用户' },
    { value: '管理员', title: '管理员' },
  ]
  settings = {
    model: 'external',

    noDataMessage: "没有数据",
    columns: {
      // id: {
      //     title: '账号',
      //     type: 'string',
      //     editable: false,

      // },
      // id: {
      //   title: '设备号',
      //   type: 'string',
      // },
      eui: {
        title: '编号',
        type: 'number'
      },
      model:{
        title:"型号",
        type: "string"
      },
      type: {
        title: '类型',
        type: 'string'
      },
      projectId:{
        title:  "项目ID",
        type: "number" 
      },
      installTime: {
        title: '安装时间',
        type: "html",
        addable:false,
        editable:false,
        filter:{
            type: 'custom', 
            component: SmartTableDatePickerComponent, 
        },
        valuePrepareFunction: (date) => { 
            var d = new Date(date)
            return d.toLocaleString('zu-ZA').slice(0,10);
        }
      },
    },
    actions: {
      columnTitle:"添加到项目",
      edit:false,
      add: false,
      position:'right'
      
    },
    delete: {
      deleteButtonContent: '<button class="btn btn-info btn-sm">添加</button>',
      confirmDelete: true,
    },
  }
  url = ConventULR("/api/sensor/v1?project=0")
  constructor(private activeModal: NgbActiveModal,
    private service: SmartTableService,
    private http: Http,
    private toasterService: ToasterService,
  ) {
    // super(http,toasterService )
    this.source = new RemoteDataSource(http, { endPoint: this.url, filterFieldKey: '#field#' });

  }
  @Output() close:EventEmitter<number> = new EventEmitter();
  closeModal() {
    this.activeModal.close();
  }
   onDeleteConfirm(event){
     // add smoke to project
     var toas = this.toasterService
     var data = event.data
     var url =  ConventULR("/api/sensor/v1")
     data['projectId'] = this.projectID
     this.http.put(url,
      data
     ).toPromise().then((res)=>{
      toas.pop("success", "添加成功")
      event.confirm.resolve()
     })
     .catch(function (err) {
      toas.pop("error", "保存失败", err["_body"])
      event.confirm.reject();
    })

   }
  add(id: any){
    console.log(id);
  }
}



@Component({
  selector: 'button-view',
  template: `
  <button class="btn btn-info btn-sm" (click)="onClick()">添加</button>
  `,
})
export class ButtonViewComponent implements ViewCell, OnInit {
  renderValue: string;
  content: "添加"

  @Input() value: string | number;
  @Input() rowData: any;
  @Output() save: EventEmitter<any> = new EventEmitter();

  ngOnInit() {
    // this.renderValue = this.value.toString().toUpperCase();
    console.log(this.rowData)
  }

  onClick(event) {
    this.save.emit(this.rowData)
    this.rowData = this.rowData
    console.log(event)
  }
}


