import { Component, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router';
import { Http } from '@angular/http';
import { ProjectService } from '../../../@core/data/projects.detail.service'
import { Location } from '@angular/common';
@Component({
    selector: 'ngx-users',
    template: `

  <div class="row"> 



  <nb-card *ngIf="smoke" class="col-md-6">
  <nb-card-header>
    {{head}}
    <button (click)="backClicked()" class="btn btn-primary btn-icon btn-tn pull-right" type="button">
            <i  class="fa fa-arrow-left" aria-hidden="true"></i>返回
        </button>
  </nb-card-header>

  <nb-card-body>
  <h3 >
     EUI：{{smoke.eui}}
  </h3>
          <div class="form-group">
              <label for="exampleInputEmail1">地址</label>
              <input [(ngModel)]="smoke.location" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
              <label for="exampleInputEmail1">状态</label>
              <input [(ngModel)]="smoke.status" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
                <label for="exampleInputEmail1">型号</label>
                <input [(ngModel)]="smoke.model" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
          <label for="exampleInputEmail1">经度</label>
          <input [(ngModel)]="smoke.longi" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
          <label for="exampleInputEmail1">纬度</label>
          <input [(ngModel)]="smoke.lati" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
          <label for="exampleInputEmail1">电话</label>
          <input [(ngModel)]="smoke.phone" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
          <label for="exampleInputEmail1">报修</label>
          <input [(ngModel)]="smoke.guarantee" disabled type="text" class="form-control">
          </div>
          <div class="form-group">
          <label >连接时间</label>
          {{smoke.ctime| date: 'yyyy-MM-dd HH:mm'}}
         </div>
         </nb-card-body>
  </nb-card>
    <nb-card *ngIf="project" class="col-md-6">
    <nb-card-header>
    项目详情
    </nb-card-header>
  
    <nb-card-body>
    <div >
    <h3 >
        项目名称：{{project.name}}
    </h3>
            <div class="form-group">
                <label for="exampleInputEmail1">省份</label>
                <input [(ngModel)]="project.province" disabled type="text" class="form-control">
            </div>
            <div class="form-group">
                <label >市</label>
                <input [(ngModel)]="project.city" disabled type="text" class="form-control">
            </div>
            <div class="form-group">
                <label >区</label>
                <input [(ngModel)]="project.district" disabled type="text" class="form-control">
            </div>
            <div class="form-group">
                <label >电话</label>
                <input [(ngModel)]="project.phone" disabled type="text" class="form-control">
            </div>
            <div class="form-group">
                <label >房间数</label>
                <input [(ngModel)]="project.roomCount" disabled type="text" class="form-control">
            </div>
            <div class="form-group">
            <label >立项时间</label>
            {{project.roomCount| date: 'yyyy-MM-dd HH:mm'}}
        </div>
            
    </div>
    
    </nb-card-body>
  </nb-card>
  </div>

  `,
    styles: [
        `
        nb-card {
            transform: translate3d(0, 0, 0);
          }
          .right-btn {
              float:right
          }
          .add-text{
            font-size: 14px;
          }
        `
    ],
    providers: [ProjectService]
})
export class EquDetailComponent  implements OnInit{
    head = "烟感器详情"
    smoke:any
    private sub: any;
    eui = ""
    project : any
    constructor(
        private route: ActivatedRoute,
        private http: Http,
        private projectService: ProjectService,
        private _location: Location,
    ){
        var mythis = this
        this.sub = this.route.params.subscribe(params => {
            mythis.eui = params['eui']
            mythis.http.get("/api/sensor/v1?project=-1&eui="+mythis.eui)
            .toPromise().then(
                (res)=>{
                    let data:Array<any> = res.json()
                    if(data.length){
                        this.smoke = data[0]
                        if(this.smoke !=0){
                            this.project = this.projectService
                            .getObject(this.smoke['projectId'])
                            .then((r)=>{
                                mythis.project = r
                                console.log(mythis.project )
                            })
                        }
                    }
                }

            )

        })

    }

    backClicked() {
        this._location.back();
    }
    ngOnInit(){

    }
}


