import { Component, AfterViewInit } from '@angular/core';

import { Cell, DefaultEditor, Editor } from 'ng2-smart-table';

@Component({
template:` <input [ngClass]="inputClass" type="date" class="form-control" [(ngModel)]="cell.newValue" [name]="cell.getId()" [disabled]="!cell.isEditable()" (click)="onClick.emit($event)" (keydown.enter)="onEdited.emit($event)" (keydown.esc)="onStopEditing.emit()">,
`
})
export class SmartTableDatePickerComponent extends DefaultEditor implements AfterViewInit {

constructor() {
super();
}

ngAfterViewInit() {

}
}


