import { Component, OnInit } from '@angular/core'
import { Project } from '../../../@core/data/models';
import { ActivatedRoute } from '@angular/router';
import { ProjectService } from "../../../@core/data/projects.detail.service"
import { Http,Response } from '@angular/http';
import { Location } from '@angular/common';
import { ToasterService } from 'angular2-toaster';

@Component({
    selector:"p-detail",
    templateUrl:"project-detail.html",
    providers:[ProjectService]
})

export class PorjectInfoComponent implements OnInit {
    file: any
    private project:Project
    constructor(private http: Http,
                private route: ActivatedRoute,
                private projects: ProjectService,
                private _location: Location,
                private toasterService: ToasterService,
    ){
        this.route.params.subscribe(params => {
            // (+) converts string 'id' to a number
            this.projects.getObject(params['id']).then(obj => {
                this.project = obj
            })
        });
    }
    ngOnInit(){

    }
    onSubmit() {
        console.log("on submit")
        let formData: FormData = new FormData(); 
        formData.append('file', this.file); 
        let url = "/api/file/v1?project="+ this.project['id']
        this.http.post(url, formData).toPromise().then((res)=>{
            this.toasterService.pop("success", "已保存")
        }
        )
        
      }
      getFiles(event){ 
        this.file = event.target.files[0]; 
        console.log(this.file)
    } 
    backClicked() {
        this._location.back();
    }
}






