import { Component, OnDestroy, OnInit } from '@angular/core'
import { TreeModel } from 'ng2-tree';
import { RouteReuseStrategy } from '@angular/router';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { CompleterService, CompleterData } from 'ng2-completer';
import { Http } from '@angular/http';
import { RemoteDataSource, ActionConfirm} from "../../../@core/utils/remoteDataSource"
import {ConventULR} from "../../pages.component"
import { ToasterService } from 'angular2-toaster';


@Component({
    selector: 'ngx-users',
    template: `
   
    <nb-card>
    <nb-card-header>
      {{head}}
    </nb-card-header>
  
    <nb-card-body>
    <ng2-smart-table [settings]="settings" 
    [source]="source"
     (createConfirm)="onCreateConfirm($event)"
     (deleteConfirm)="onDeleteConfirm($event)"
     (editConfirm)="onEditConfirm($event)">
    </ng2-smart-table> 
    </nb-card-body>
  </nb-card>
  `,
    styles: [
        `
        nb-card {
            transform: translate3d(0, 0, 0);
          }
          .right-btn {
              float:right
          }
          .add-text{
            font-size: 14px;
          }
        `
    ]
})
export class EquipmentComponent extends ActionConfirm implements OnInit{
    head = "运维管理"
    settings = {
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input",
            confirmCreate: true,
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            confirmSave: true,
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
            eui: {
                title: 'EUI',
                type:'html',
                valuePrepareFunction:(cell,row)=>{
                  return `<a  href="#/pages/admin/equipments/${row.eui}">${row.eui}</a>`
                },
                editable:false
            },
            opUser: {
                title: '运维用户',
                type: 'string',
            },
            // project: {
            //     title: '省市',
            //     type: 'string',
            //     valuePrepareFunction: (val)=>{
            //         if(val){
            //            return val['province']+ val['city']+val['district']
            //         }
            //     },
            //     filter:false,
            //     editable:false 
            // },
            sensor: {
                title: '地址',
                type: 'string',
                valuePrepareFunction: (val)=>{
                    if(val){
                       return  val['location']
                    }
                },
                editable:false
            },
            fixUser:{
                title: "维保人员",
                type: 'string'
            },
            ctime:{
                title: "故障时间",
                type: 'string',
                valuePrepareFunction: (date) => { 
                    var d = new Date(date)
                    return d.toLocaleString('zu-ZA').slice(0,10);
                }
            },
            cause:{
                title: "故障原因",
                type:"string"
            },
            opTime:{
                title: "维保时间",
                type: 'string',
                valuePrepareFunction: (date) => { 
                    var d = new Date(date)
                    return d.toLocaleString('zu-ZA').slice(0,10);
                }
            },
            handler:{
                title: "处理方式",
                type: 'string'
            },
            status:{
                title:"状态",
                type: "string"
            }
       },
        actions: {
            columnTitle: "操作",
            add: false,

        },
        noDataMessage:"没有数据",
    }
    source: RemoteDataSource;
    url = ConventULR("/api/op/v1")
    constructor(private service: SmartTableService,
            http: Http,
            toasterService: ToasterService
        ) {
        super(http,toasterService )
        const data = this.service.getData();
        this.source = new RemoteDataSource(http, { endPoint: this.url, filterFieldKey: '#field#' });

    }
    ngOnInit() {
        console.log("init")
    }


}


