import { Component, OnInit } from '@angular/core'
import { ProjectService } from "../../../@core/data/projects.detail.service"
import { Project } from "../../../@core/data/models"
import { ActivatedRoute } from '@angular/router';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { Http } from '@angular/http';
import { Location } from '@angular/common';
import { LocalDataSource } from 'ng2-smart-table';
import { RemoteDataSource, ActionConfirm } from "../../../@core/utils/remoteDataSource"
import { ConventULR } from "../../pages.component"
import { ToasterService } from 'angular2-toaster';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UserModalComponent } from './user.add.component'
@Component({
    selector: "project-detail",
    templateUrl: './detail.html',
    styleUrls: ['./project.scss'],
    providers: [ProjectService]
})
export class ProjectDetailComponent implements OnInit {
    private sub: any;
    private project: Project
    settings = {
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input",
            confirmCreate: true,
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            confirmSave: true,
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
            id: {
                title: "用户ID",
                type: "string"
            },
            name: {
                title: "用户名",
                type: "string"
            },
            phonenumber1: {
                title: "电话1",
                type: "string"
            },
            phonenumber2: {
                title: "电话2",
                type: "string"
            },
            regTime: {
                title: "注册时间",
                editable: false,
                type: "string",
                add: false
            }
        },
        actions: {
            columnTitle: "操作",
            add: false,
            edit: false
        },
        setPaging: {
            page: 20,
            perPage: 10
        },
        setPage: {
            page: 20,
            perPage: 10
        },
        count: 20000,

    }
    source: LocalDataSource;
    url = ConventULR("");
    porject: any
    projectName = ""
    projectID = ""
    constructor(private projects: ProjectService,
        private route: ActivatedRoute,
        private service: SmartTableService,private http: Http,
        private _location: Location,
        private modalService: NgbModal,
        private toasterService: ToasterService
    ) {
        this.sub = this.route.params.subscribe(params => {
            // (+) converts string 'id' to a number
            this.projects.getObject(params['id']).then(obj => {
                this.porject = obj
                this.projectID = obj["id"]
                this.projectName = obj['name']
                this.source = new LocalDataSource(obj["users"])
            })
        });
    }

    loadData() {
        this.projects.getObject(this.projectID).then(obj => {
            this.source = new LocalDataSource(obj["users"])
        })
    }
    backClicked() {
        this._location.back();
    }
    ngOnInit() {
    }
    showLargeModal() {
        const activeModal = this.modalService.open(UserModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = '添加用户';
        activeModal.componentInstance.projectID = this.porject.id
        activeModal.result.then(
            (s) => {
                this.loadData()
            }

        )
    }
    onDeleteConfirm(event): void {
        var toas = this.toasterService
        let url = "/api/up/v1/" +event["data"]['id'] + "/" + this.porject.id
         if (window.confirm('确定删除吗？')) {
			console.log(event)
			this.http.delete(url).toPromise()
			.then(function(res){
				toas.pop("success", "删除成功")
				event.confirm.resolve();	
			})
			.catch(
				function(err){
				    console.log(err)
					toas.pop("error", "删除失败", err["_body"])
					event.confirm.reject();	
				}
			)
        } else {
            event.confirm.reject();
        }
	}
}


