import { NgModule } from '@angular/core';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { SmartTableService } from '../../@core/data/smart-table.service';
import { SmartTableDatePickerComponent } from '../../@core/utils/smart-table-date-picker.component';

import { TreeModule } from 'ng2-tree';
import { ToasterModule } from 'angular2-toaster';

import { ThemeModule } from '../../@theme/theme.module';
import { ComponentsRoutingModule, routedComponents } from './admin-routing.module';
import {ModalComponent} from "./smoke/modal.component"
import {UserModalComponent} from "./project/user.add.component"
@NgModule({
  imports: [
    ThemeModule,
    ComponentsRoutingModule,
    TreeModule,
    ToasterModule,
    Ng2SmartTableModule,
    
  ],
  declarations: [
    ...routedComponents,
    SmartTableDatePickerComponent,
    ModalComponent,
    UserModalComponent
  ],
  providers: [
    SmartTableService,
    SmartTableDatePickerComponent,
    UserModalComponent
  ],
  entryComponents: [
    SmartTableDatePickerComponent,
    ModalComponent,
    UserModalComponent
  ]
})
export class AdminModule { }


