import { Component, OnDestroy, OnInit } from '@angular/core'
import { TreeModel } from 'ng2-tree';
import { RouteReuseStrategy , ActivatedRoute} from '@angular/router';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { CompleterService, CompleterData } from 'ng2-completer';
import { Http } from '@angular/http';
import { RemoteDataSource, ActionConfirm} from "../../../@core/utils/remoteDataSource"
import {Location} from '@angular/common';
import {ConventULR} from "../../pages.component"
import { ToasterService } from 'angular2-toaster';
import {ProjectService} from "../../../@core/data/projects.detail.service"
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {ModalComponent} from './modal.component'
@Component({
    selector: 'ngx-users',
    template: `
    <nb-card>
    <nb-card-header>
      {{head}}
    </nb-card-header>

    <nb-card-body>
    <ng2-smart-table [settings]="settings" [source]="source"  (editConfirm)="onEditConfirm($event)" (deleteConfirm)="onDeleteConfirm($event)">
    </ng2-smart-table>
    </nb-card-body>
  </nb-card>
  `,
    styles: [`
  nb-card {
    transform: translate3d(0, 0, 0);
  }
  .right-btn {
      float:right
  }
  .add-text{
    font-size: 14px;
  }

`],
providers: [ProjectService]
})
export class ALLSmokeComponent extends ActionConfirm implements OnInit {
    head = "设备管理"
    type_filter = [
        { value: '火警', title: '火警' },
        { value: '正常', title: '正常' },
        { value: '已报警', title: '已报警' },
        { value: '低电压', title: '低电压' },
    ]
    settings = {
        noDataMessage:"没有数据",
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input",
            confirmCreate: true,
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            confirmSave: true,
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
        //    id : {
        //         title: 'id',
        //         type: 'string',
        //     },
            eui: {
                title: '型号',
                type: 'html',
                valuePrepareFunction:(cell,row)=>{
                    return `<a  href="#/pages/admin/equipments/${row.eui}">${row.eui}</a>`
                  },
                  editable:false
            },
            status: {
                title: '状态',
                type: 'string',
                editor:{
                    type:"list",
                    config: {
                        selectText: '选择类别',
                        list: this.type_filter ,
                    },
                },
                filter: {
                    type: 'list',
                    config: {
                        selectText: '选择类别',
                        list: this.type_filter,
                    },
                },
            },
            type: {
                title: '类型',
                type: 'string',
                editable: false
            },
            phone:{
                title:"联系人电话",
                type: "string"
            },
            location: {
                title: '位置',
                type: 'string'
            },
            installTime: {
                title: '安装时间',
                type: 'string',
                valuePrepareFunction: (date) => {
                    var d = new Date(date)
                    return d.toLocaleString('zu-ZA').slice(0,10);
                },
                editable: false
            },
            guarantee: {
                title: '保修情况',
                type: 'string',
                editable: false
            },

        },
        actions:{
            columnTitle:"操作",
            editable: false
        },
    }
    source: RemoteDataSource;
    url= ConventULR("/api/sensor/v1?project=-1")
    porject: any
    private sub: any;
    subscription: any;
    htp: Http
    constructor(private service: SmartTableService,
        http: Http,
        public myHttp:Http,
        private projects: ProjectService,
        private route: ActivatedRoute,
        // params: Params,
        private _location: Location,
        private modalService: NgbModal,
        toasterService: ToasterService
    )
    {
        super(http, toasterService)
        console.log("in smole")
        this.htp = http
        this.source = new RemoteDataSource(http, { endPoint: this.url,  filterFieldKey: '#field#' });
    }
    onDeleteConfirm(event): void {
      var toas = this.toasterService
           if (window.confirm('确定删除吗？')) {
        console.log("hello")
        this.htp.delete("/api/sensor/v1/"+ event.data["id"]).toPromise()
        .then(function(res){
          toas.pop("success", "删除成功")
          event.confirm.resolve();
        })
        .catch(
          function(err){
              console.log(err)
            toas.pop("error", "删除失败", err["_body"])
            event.confirm.reject();
          }
        )
          } else {
              event.confirm.reject();
          }
    }
    ngOnInit() {
        console.log("init")
    }

}



