import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdminComponent } from './admin.component';
import { UserComponent } from './users/users.component';
import {EquipmentComponent} from './equipment/equipment.component'
import {EquDetailComponent} from './equipment/detail'
import {ProjectComponent} from './project/project.component'
import {ButtonViewComponent} from "./project/button.component"
import {ProjectDetailComponent} from "./project/detail.component"
import { SmokeComponent} from "./smoke/smoke.component"
import { ALLSmokeComponent} from "./smoke/all.smoke.component"
import { AdminGatewayComponent} from "./smoke/gateway.component"
import {PorjectInfoComponent} from "./project/project-detail"

const routes: Routes = [{
  path: '',
  component: AdminComponent,
  children: [
  {
    path: 'users',
    component: UserComponent,
  },
  {
    path: 'equipment',
    component: EquipmentComponent,
  },
  {
    path: 'equipments/:eui',
    component: EquDetailComponent,
  },
  {
    path: 'projects',
    component: ProjectComponent,
  },
  {
    path: 'project/:id',
    component: ProjectDetailComponent,
  },
  {
    path: 'pic/:id',
    component: PorjectInfoComponent,
  },
  {
    path: 'smoke/:id',
    component: SmokeComponent,
  },
  {
    path: 'smokes',
    component: ALLSmokeComponent,
  },
  {
    path: 'gateway',
    component: AdminGatewayComponent,
  },

  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  entryComponents:[ButtonViewComponent]
})
export class ComponentsRoutingModule { }

export const routedComponents = [
  AdminComponent,
  UserComponent,
  EquipmentComponent,
  ProjectComponent,
  ButtonViewComponent,
  ProjectDetailComponent,
  SmokeComponent,
  AdminGatewayComponent,
  ALLSmokeComponent,
  EquDetailComponent,
  PorjectInfoComponent
];


