
import { Component, OnInit } from '@angular/core'
import { RouteReuseStrategy } from '@angular/router';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import {SmartTableDatePickerComponent  } from '../../../@core/utils/smart-table-date-picker.component';
import { RemoteDataSource, ActionConfirm} from "../../../@core/utils/remoteDataSource"
import {ConventULR} from "../../pages.component"
import { ToasterService } from 'angular2-toaster';
import { CompleterService, CompleterData } from 'ng2-completer';
import { Http } from '@angular/http';
import {ButtonViewComponent} from "./button.component"
import { filter } from 'rxjs/operators/filter';
@Component({
    selector: 'admin-project',
    styleUrls: ['./project.scss'],
    templateUrl: './project.html',
})
export class ProjectComponent extends ActionConfirm implements OnInit {

    head = "项目管理"
    settings = {
        noDataMessage:"没有数据",
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            inputClass: "add-input",
            confirmCreate: true,
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
            confirmSave: true,
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
            confirmDelete: true,
        },
        columns: {
            Actions: //or something
            {
              title:'更多操作',
              type:'html',
              valuePrepareFunction:(cell,row)=>{
                return `
                <a href="#/pages/admin/project/${row.id}">用户</a>
                <a href="#/pages/admin/smoke/${row.id}">设备</a> 
                `
              },
              filter:false,
              addable: false, 
            },
            name :{
                title:"项目名称",
                type:'html',
                valuePrepareFunction:(cell,row)=>{
                    return `
                    <a href="#/pages/admin/pic/${row.id}">${row.name}</a>
                    `
                }, 
            },

            phone:{
                title:"联系人电话",
                type: "string"
            },

            province:{
                title:'省份',
                type:'string',
            },
            city: {
                title:'市',
                type:'string'
            },
            district:{
                title:'区/县',
                type:'string' 
            },
            address:{
                title:"地址",
                type:"string"
            },
            // area:{
            //     title:"占地面积",
            //     type:"string"
            // },
            roomCount:{
                title:"房间数",
                type:"int"
            },
            ctime :{
                title:"立项时间",
                type: "html",
                addable:false,
                editable:false,
                filter:{
                    type: 'custom', 
                    component: SmartTableDatePickerComponent, 
                },
                valuePrepareFunction: (date) => { 
                    var d = new Date(date)
                    return d.toLocaleString('zu-ZA').slice(0,10);
                }
            },
            smokeWathcerCount:{
                title:'烟感数量',
                type:"int",
                addable: false, 
                editable: false
            },
            
       },
        actions: {
            columnTitle: "操作",
            add: true,
            position:"right"
        },
    }
    source: RemoteDataSource;
    url = ConventULR("/api/project/v1")
    constructor(private service: SmartTableService,
         http: Http,
         toasterService: ToasterService
        ) {
        super(http,toasterService )
        const data = this.service.getData();
        this.source = new RemoteDataSource(http, { endPoint: this.url, filterFieldKey: '#field#' });
    }

    ngOnInit() {
        console.log("init")
    }
}


